import React from 'react'
import { Link } from 'gatsby'

export default tabNav

function tabNav({ currentCoursePath, type }) {
  return (
    <ul className="nav nav-tabs">
      <li className="nav-item">
        <Link
          className={
            type === `course` ? `nav-link disabled active` : `nav-link`
          }
          to={type === `course` ? `#` : `/${currentCoursePath}`}
        >
          Cours
        </Link>
      </li>
      <li className="nav-item">
        <Link
          className={type === `quiz` ? `nav-link disabled active` : `nav-link`}
          to={type === `quiz` ? `#` : `/${currentCoursePath}/quiz`}
        >
          Quiz
        </Link>
      </li>
      <li className="nav-item">
        <Link
          className={type === `exo` ? `nav-link disabled active` : `nav-link`}
          to={type === `exo` ? `#` : `/${currentCoursePath}/exo`}
        >
          Exercices
        </Link>
      </li>
      {/* <li className="nav-item">
        <Link
          className={type === `diapo` ? `nav-link disabled active` : `nav-link`}
          to={type === `diapo` ? `#` : `/${currentCoursePath}/diapo`}
        >
          Diapo
        </Link>
      </li> */}
    </ul>
  )
}
